<template>
  <div class="rights">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>权限列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table :data="allRights" stripe style="width: 100%" border>
        <el-table-column label="#" width="80" type="index"> </el-table-column>
        <el-table-column prop="authName" label="授权名称"> </el-table-column>
        <el-table-column prop="path" label="路径"> </el-table-column>
        <el-table-column prop="level" label="权限等级">
          <template v-slot="scope">
            <el-tag v-if="scope.row.level === '0'">一级</el-tag>
            <el-tag type="success" v-else-if="scope.row.level === '1'"
              >二级</el-tag
            >
            <el-tag type="warning" v-else>三级</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  created() {
    this.getAllRights("list");
  },
  computed: {
    ...mapState({
      allRights: (state) => state.roles.allRights,
    }),
  },
  methods: {
    ...mapActions(["getAllRights"]),
  },
};
</script>

<style lang="less">
.rights {
  .el-breadcrumb {
    font-size: 12px;
    margin-bottom: 20px;
  }
  .el-table {
    font-size: 12px;
  }
}
</style>
